import * as React from "react";
import  '../../css/ObjectPanel.css';

export class ObjectPanel extends React.Component<{}, {}> {
  render() {
    return <div className="painter-object-panel">
      <input title="" className="color object-color"/>
      <label className="object-opacity">  -
        <input title=" " type="range" min="0" max="1" step="0.01"/>+
        <span>Opacity</span>
      </label>
      <input title="" type="number" className="font-size"/>
      <textarea className="object-text" title=""/>
      <div title="" className="remove-button">
        <img src="//o44j7l4g3.qnssl.com/program/painter/delete.png" alt=""/>
      </div>
    </div>;
  }
}


