import * as React from "react";
import  '../../css/painter-tools-painting.css';

import { config } from '../../config';
import { ColorButton } from '../buttons/ColorButton';

interface PaintingPanelProps {
  className: string;
  current_color: string;
}

export class PaintingPanel extends React.Component<PaintingPanelProps, {}> {
  render() {
    let default_colors = config.default_colors.map((value) => {
      return <ColorButton key={value} color={value} checked={ value === this.props.current_color }
                          onClick={this.select_color}/>;
    });
    return <div className="tools-painting">
      <div className="tools-buttons-container">
        <div title="" className="tools-button tools-pencil">
          <img className="button-img" src="//o44j7l4g3.qnssl.com/program/painter/pencil.png" alt=""/>
          <img className="button-img-on" src="//o44j7l4g3.qnssl.com/program/painter/pencil-on.png" alt=""/>
        </div>
        <div title="" className="tools-button tools-line">
          <img className="button-img" src="//o44j7l4g3.qnssl.com/program/painter/line.png" alt=""/>
          <img className="button-img-on" src="//o44j7l4g3.qnssl.com/program/painter/line-on.png" alt=""/>
        </div>
        <div title="" className="tools-button tools-rect">
          <img className="button-img" src="//o44j7l4g3.qnssl.com/program/painter/rect.png" alt=""/>
          <img className="button-img-on" src="//o44j7l4g3.qnssl.com/program/painter/rect-on.png" alt=""/>
        </div>
        <div title="" className="tools-button tools-round">
          <img className="button-img" src="//o44j7l4g3.qnssl.com/program/painter/round.png" alt=""/>
          <img className="button-img-on" src="//o44j7l4g3.qnssl.com/program/painter/round-on.png" alt=""/>
        </div>
        <div title="" className="tools-button tools-triangle">
          <img className="button-img" src="//o44j7l4g3.qnssl.com/program/painter/triangle-off.png" alt=""/>
          <img className="button-img-on" src="//o44j7l4g3.qnssl.com/program/painter/triangle-on.png" alt=""/>
        </div>
      </div>
      <div className="tools-buttons-container">
        <div title="" className="tools-button tools-eraser">
          <img className="button-img" src="//o44j7l4g3.qnssl.com/program/painter/eraser.png" alt=""/>
          <img className="button-img-on" src="//o44j7l4g3.qnssl.com/program/painter/eraser-on.png" alt=""/>
        </div>
        <div title="" className="tools-button tools-select">
          <img className="button-img" src="//o44j7l4g3.qnssl.com/program/painter/select.png" alt=""/>
          <img className="button-img-on" src="//o44j7l4g3.qnssl.com/program/painter/select-on.png" alt=""/>
        </div>
        <div title="" className="tools-button tools-text">
          <img className="button-img" src="//o44j7l4g3.qnssl.com/program/painter/text.png" alt=""/>
          <img className="button-img-on" src="//o44j7l4g3.qnssl.com/program/painter/text-on.png" alt=""/>
        </div>
        <div title="" className="tools-button tools-select">
          <img className="button-img" src="//o44j7l4g3.qnssl.com/program/painter/rotation.png" alt=""/>
          <img className="button-img-on" src="//o44j7l4g3.qnssl.com/program/painter/rotation-on.png" alt=""/>
        </div>
      </div>
      <div className="tools-slider-container">
        <span className="tools-container-title"></span>
        <span className="input-minus">-</span>
        <input title="" type="range" value="7" min="1" max="100" step="1"/>
        <span className="input-plus">+</span>
      </div>
      <div className="tools-item">
        <span className="item-title"></span>
        <input title="" type="color" className="color"/>
      </div>
      <div className="tools-area">
        <div className="tools-default-color">
        </div>
        {default_colors}
      </div>
    </div>;
  }
}


